<template>
  <div class="page-container">
    <el-card class="basic-card">
      <el-form ref="profile" :model="profile" :rules="rules" label-width="120px" size="small" label-suffix=" : ">
        <el-form-item label="实体名称" prop="name">
          <!-- <el-input v-model="profile.name" disabled=true style="width:500px;"></el-input> -->
          <span>{{ profile.name }}</span>
        </el-form-item>
        <el-form-item label="会员身份" prop="entity_type">
          <!-- <el-select v-model="profile.entity_type" placeholder="请选择实体类型" disabled=true>
            <el-option label="企业" value="enterprise"></el-option>
            <el-option label="政府" value="government"></el-option>
            <el-option label="学校" value="education"></el-option>
            <el-option label="非盈利组织" value="non-profit"></el-option>
            <el-option label="慈善机构" value="charity"></el-option>
            <el-option label="其它" value="other"></el-option>
          </el-select> -->
          <span>{{ profile.entity_type }}</span>
        </el-form-item>
        <el-form-item label="活动区域" prop="region">
          <el-select v-model="profile.region" placeholder="请选择活动区域">
            <el-option label="东北" value="northeastern" />
            <el-option label="华北" value="northern" />
            <el-option label="华东" value="eastern" />
            <el-option label="华中" value="central" />
            <el-option label="华南" value="southern" />
            <el-option label="西北" value="northwestern" />
            <el-option label="西南" value="southewestern" />
          </el-select>
        </el-form-item>
        <el-form-item label="营业年限" prop="period">
          <el-select v-model="profile.period" placeholder="请选择营业年限">
            <el-option label="3年以内" value="3" />
            <el-option label="5年以内" value="5" />
            <el-option label="10年以内" value="10" />
            <el-option label="10年以上" value="30" />
          </el-select>
        </el-form-item>
        <el-form-item label="企业规模" prop="staff_size">
          <el-select v-model="profile.staff_size" placeholder="请选择企业规模">
            <el-option label="100人以下" value="100" />
            <el-option label="100到300人" value="300" />
            <el-option label="300到500人" value="500" />
            <el-option label="500人以上" value="1000" />
          </el-select>
        </el-form-item>
        <div style="margin-top:30px; margin-bottom:10px">联系信息</div>
        <el-form-item label="国家/地区" prop="country">
          <el-input v-model="profile.country" disabled="true" style="width:200px;" />
        </el-form-item>
        <el-form-item label="所在地区" prop="position">
          <area-selector ref="areaSelector" />
        </el-form-item>
        <el-form-item label="详细地址" prop="address">
          <el-input v-model="profile.address" style="width:410px;" />
        </el-form-item>
        <el-form-item label="联系电话" prop="tel">
          <el-input v-model="profile.tel" style="width:410px;" placeholder="例如: 010-88888888" />
        </el-form-item>

      </el-form>
    </el-card>
  </div>
</template>

<script>
import AreaSelector from '@/components/AreaSelector'
import { mapGetters } from 'vuex'
import { getInfo } from '@/api/enterprise'
import { validFixedLinePhone } from '@/utils/validate'

export default {
  name: 'Profile',
  components: {
    AreaSelector
  },
  data() {
    var checkTel = (rule, value, callback) => {
      if (!validFixedLinePhone(value)) {
        return callback(new Error('格式不对'))
      }
    }
    return {
      loading: true,
      profile: {
        name: '',
        entity_type: '',
        region: '',
        period: '',
        staff_size: '',
        country: '',
        province: '',
        address: ''
      },
      rules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' },
          { min: 5, message: '请输入完整名称', trigger: 'blur' }
        ],
        entity_type: [
          { required: true, message: '请选择实体类型', trigger: 'change' }
        ],
        region: [
          { required: true, message: '请选择活动区域', trigger: 'change' }
        ],
        period: [
          { required: true, message: '请选择营业年限', trigger: 'change' }
        ],
        staff_size: [
          { required: true, message: '请选择企业规模', trigger: 'change' }
        ],
        tel: [
          { validator: checkTel, trigger: 'blur' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters({
      uscc: 'user/uscc'
    })
  },
  created() {
    this.fetchData()
  },
  methods: {
    fetchData() {
      this.loading = true
      getInfo(this.uscc)
        .then((response) => {
          const { status, data } = response.data
          this.profile = data[0]
        })
        .finally(() => {
          this.loading = false
          this.$refs.areaSelector.province = this.profile.province
        })
    },
    onSubmit() {
      console.log('submit!')
    }
  }
}
</script>

<style lang="scss" scoped>
  .profile-form {
  }
</style>
